import React from "react"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

export default function GaDisable({ disabled }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            siteUrl
            title
            description
            author
            ogpImageUrl
          }
        }
      }
    `
  )

  const gaMeasurementId = site.siteMetadata?.gaMeasurementId
  const disableStr = "ga-disable-${gaMeasurementId}";

  return (
    <Helmet>   
      { disabled && (
        
          <script>
            var disableStr = "ga-disable-{gaMeasurementId}";
            
          </script>
        
      )}
    </Helmet>
  )
}