import React from "react"
import { Link } from "gatsby"
import { useLocation } from "@reach/router"
//import { initializeAndTrack } from 'gatsby-plugin-gdpr-cookies'
import CookieConsent, { Cookies, getCookieConsentValue } from "react-cookie-consent";
import "@fontsource/noto-sans-jp";
import Header from "./header"
import Footer from "./footer"
import ScrollTop from "./scroll-top"
import GaDisable from "./ga-disable";

export default function Layout({ children }) {
  //console.log("CookieConsentValue=[" + getCookieConsentValue() + "]");
  //const location = useLocation()
  const gaDisable = !getCookieConsentValue();
  return (
    <>
      <CookieConsent
        location="bottom"
        buttonText="Cookie使用を許可"
        style={{ background: "#2B373B", fontSize: "12px" }}
        buttonStyle={{ color: "#4e503b", fontSize: "13px" }}
        enableDeclineButton
        declineButtonText="Cookie使用を拒否"
        declineButtonStyle={{ color: "#ffffff", fontSize: "13px" }}
        expires={150}
        onAccept={() => {
          //console.log("CookieConsentValue=[" + getCookieConsentValue() + "]");
          //initializeAndTrack(location)
        }}
        onDecline={() => {
          //console.log("CookieConsentValue=[" + getCookieConsentValue() + "]");
          //window["ga-disable-${process.env.GA_TRACKING_ID}"] = true;
        }}
      >
        当サイトでは、お客様のアクセス傾向を分析してより良いサービスを提供するため、Cookieを使用いたします。{" "}
        <span style={{ fontSize: "10px" }}>
          <Link to={`/privacy-policy`}>プライバシーポリシー</Link>
        </span>
      </CookieConsent>

      <GaDisable disabled={true} />
      <Header/>
      <main>{children}</main>
      <Footer/>
      <ScrollTop/>
    </>     
  )
}
